<template>
  <div class="bg">
    <div style="display: block">
      <p>TOYCHAT</p>
      <a href="http://localhost:9090/oauth2/authorization/kakao">
        <img src="@/assets/images/login/kakao_btn.webp" width="50"
      /></a>
      <a href="http://localhost:9090/oauth2/authorization/naver">
        <img src="@/assets/images/login/naver_btnD_아이콘원형.png" width="50"
      /></a>
      <a href="http://localhost:9090/oauth2/authorization/google">
        <img
          src="@/assets/images/login/google_web_neutral_rd_na@4x.png"
          width="50"
      /></a>
      <a href="http://localhost:9090/oauth2/authorization/github">
        <img src="@/assets/images/login/github.png" width="50"
      /></a>
    </div>
  </div>
</template>

<script>
export default {
  methods: {
    // 안씀
    fn_kakaoLogin() {
      window.Kakao.Auth.login({
        scope: "profile_nickname",
        success: this.fn_getKakaoAccount,
      });
    },
    // 안씀
    fn_getKakaoAccount() {
      window.Kakao.API.request({
        url: "/v2/user/me",
        success: (res) => {
          var id = res.id;
          var nick = res.kakao_account.profile.nickname;

          //로그인 여부 저장
          localStorage.setItem("isAuthenticated", true);
          localStorage.setItem("id", id);
          localStorage.setItem("nick", nick);
          localStorage.setItem("role", "USR");
          this.$router.go("/");
        },
        fail: (error) => {
          console.log(error);
          localStorage.setItem("isAuthenticated", false);
        },
      });
    },
  },
};
</script>

<style>
@import "@/assets/css/base.css";
@import "@/assets/css/user.css";
</style>
